/* ExpenseList.css */

/* Container styles */
.expense-list-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

/* Heading styles */
.expense-list-container h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

/* Table styles */
.expense-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

/* Table header styles */
.expense-table th {
  text-align: left;
  padding: 10px;
  background-color: #ccc7c7;
}

/* Table cell styles */
.expense-table td {
  padding: 10px;
  background-color: #5a5858;
  border-bottom: 1px solid #e0e0e0;
}

/* Actions column styles */
.expense-table td:last-child {
  white-space: nowrap; /* Prevent button wrapping */
}

/* Button styles */
.expense-table button {
  padding: 5px 10px;
  margin-right: 5px;
  cursor: pointer;
  background-color: #8693a0;
  color: #fff;
  border: none;
  border-radius: 3px;
  transition: background-color 0.3s;
}

.expense-table button:hover {
  background-color: #0056b3;
}

/* Edit form container styles */
.edit-form-container {
  margin-top: 20px;
}

/* Date box styles */
/* Date box styles */
.date-box {
  display: flex;
  flex-direction: column;
  align-items: center; /* Align items vertically */
}

.date-box > div {
  padding: 5px;
  text-align: center;
}

/* Separate styles for each date element */
.month,
.year,
.day {
  width: 100%; /* Occupy full width */
}

/* Square box styles */
.date-box-container {
  width: 80px; /* Adjust the width of the box */
  height: 90px; /* Set the height to accommodate all date elements */
  border: 1px solid #ccc; /* Add borders for visual separation */
  border-radius: 5px; /* Add rounded corners */
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: #686262;
}


/* Media query for responsiveness */
@media (max-width: 600px) {
  .expense-table th,
  .expense-table td {
    font-size: 14px;
  }
}

